<script>

import {
  OKX_CONNECT_ERROR_CODES
} from "@okxconnect/core"
import { OKXUniversalProvider } from "@okxconnect/universal-provider"
import {OKXSolanaProvider} from "@okxconnect/universal-provider/OKXSolanaProvider";
import {PublicKey, Transaction} from '@solana/web3.js';


var chains = new Set([1]);
var optionChains = new Set([])

// import QrcodeVue from 'qrcode.vue'
export default {
  name: 'App',
  // components: {
  //   QrcodeVue
  // },
  data: () => {
    return {
      activeTab: 'eip155:1',
      testTopic: '',
      testPublicKey: '',
      testProofPayload: '',
      pendingDeepLink: '-',
      logList: [],
      connectReady: false,
      connectStatusStr: '连接钱包',
      disconnectStatusStr: '断开钱包',
      localPubKey: '',
      localSecKey: '',
      testTransaction: '',
      selectedChain: 'eip155:1',
      testTransactionStr: '发送交易',
      walletAddress: '',
      testboc: '',
      ProofPayloadVerify: '',
      showModal: false,
      showVerifyRow: false,
      qrValue: 'https://www.okx.com/download',
      currentStatus: "",
      isChecked: (chainId) => {
        return chains.has(chainId)
      },
      isOptionalChecked: (chainId) => {
        return optionChains.has(chainId)
      },
      handleChange: (chainId) => {
        if (chains.has(chainId)) {
          chains.delete(chainId)
        } else {
          chains.add(chainId)
        }
      },
      handleOptionChange: (chainId) => {
        if (optionChains.has(chainId)) {
          optionChains.delete(chainId)
        } else {
          optionChains.add(chainId)
        }
      },
    }
  },
  computed: {
    getWalletAddress: function () {
      return this.walletAddress;
    },

  },
  methods: {

    async clickActiveTab(tab) {

      const tabName = tab.props ? tab.props.name : tab.name;
      this.selectedChain = tabName
      console.log(`已切换到链 :`, tabName);

      if (!window.provider) {
        console.error('钱包 未初始化完成');
      }
      window.provider.setDefaultChain(this.selectedChain);
    },

    async connectBtnClicked() {
      console.log(`encryptStoreMsg   connectBtnClicked  ${Date.now()}`);
      if (window.provider) {
        this.testTonSdk();
      } else {
        console.log(`connectBtnClicked provider not finish init`);
      }

    },

    async disconnectAction() {
      try {
        console.log('get disconnect wallet btn clicked');
        // await TON.disconnect();
        var provider = window.provider
        await provider.disconnect();
        this.walletAddress = 'disconnected'
      } catch (error) {
        console.log('Disconnect  error ==>> ', error.message);
        this.currentStatus = error.message;
      }
    },

    async eth_requestAccounts() {
      var data = { "method": "eth_requestAccounts" }
      this.testTransaction = JSON.stringify(data)
    },
    async eth_chainId() {
      var data = { "method": "eth_chainId" }
      this.testTransaction = JSON.stringify(data)
    },
    async wallet_switchEthereumChain() {
      var data = { "method": "wallet_switchEthereumChain" }
      this.testTransaction = JSON.stringify(data)
    },

    async wallet_addEthereumChain() {
      var data = {
        "method": "wallet_addEthereumChain",
